import React from 'react'
import { useState } from 'react'

const CreateNote = ({ onAdd }) => {

    const [note, setNote] = useState({
        title: '', content: ''
    })

    const handleChange = (e) => {
        const { name, value } = e.target
        setNote(prevNote => {
            return {
                ...prevNote, [name]: value
            }
        })
    }

    const submitForm = (e) => {
        onAdd(note)
        e.preventDefault()
    }


    return (
        <div className='container'>
            <form className='row'>
                <div className="col-6 mx-auto">
                    <input name='title' type="text" onChange={handleChange} value={note.title} placeholder='Title' className='form-control border-bottom border-0' />
                    <textarea name='content' onChange={handleChange} value={note.content} className='form-control mb-2 border-0' placeholder='Add a note...'></textarea>
                    <div className="text-center">
                        <button onClick={submitForm} className='btn border btn-secondary'>Add</button>
                    </div>
                </div>
            </form>
        </div>
    )
}

export default CreateNote
