import React from 'react'
import styled from 'styled-components'

const Footer = () => {
    return (
        <Wrapper className='footer'>
            <p>&copy; {new Date().getFullYear()}</p>
        </Wrapper>
    )
}

const Wrapper = styled.footer`
    position: absolute;
    text-align:center;
    bottom: 0;
    height: 2.5%;
    width: 100%;
    color: #fff;
    height: 5vh;
   
`
export default Footer
